import React, { Component } from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import AppBar from "material-ui/AppBar";
import { List, ListItem } from "material-ui/List";
import RaisedButton from "material-ui/RaisedButton";

export class FormPersonalDetails extends Component {
  continue = e => {
    e.preventDefault();
    // PROCESS FORM
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  render() {
    const {
      values: {
        firstName,
        surname,
        email,
        gender,
        phone,
        address,
        blood,
        allergies,
        conditions,
        medications
      }
    } = this.props;
    return (
      <MuiThemeProvider>
        <React.Fragment>
          <AppBar title="Please Confirm Your Data" />
          <List>
            <ListItem primaryText="First Name" secondaryText={firstName} />
            <ListItem primaryText="Last Name" secondaryText={surname} />
            <ListItem primaryText="Gender" secondaryText={gender} />
            <ListItem primaryText="Phone Number" secondaryText={phone} />
            <ListItem primaryText="Email" secondaryText={email} />
            <ListItem primaryText="Address" secondaryText={address} />
            <ListItem primaryText="Blood Group" secondaryText={blood} />
            <ListItem primaryText="Allergies" secondaryText={allergies} />
            <ListItem
              primaryText="Medical Conditions"
              secondaryText={conditions}
            />
            <ListItem primaryText="Medications" secondaryText={medications} />
          </List>
          <br />
          <RaisedButton
            label="Confirm & Continue"
            primary={true}
            style={styles.button}
            onClick={this.continue}
          />
          <RaisedButton
            label="Back"
            primary={false}
            style={styles.button}
            onClick={this.back}
          />
        </React.Fragment>
      </MuiThemeProvider>
    );
  }
}

const styles = {
  button: {
    margin: 15
  }
};

export default FormPersonalDetails;
