import React, { Component } from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import AppBar from "material-ui/AppBar";
import TextField from "material-ui/TextField";
import RaisedButton from "material-ui/RaisedButton";

export class FormPersonalDetails extends Component {
  continue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  render() {
    const { values, handleChange } = this.props;
    return (
      <MuiThemeProvider>
        <React.Fragment>
          <AppBar title="Please Enter Your Medical Information" />
          <TextField
            hintText="Enter your Blood Group"
            floatingLabelText="Blood Group"
            onChange={handleChange("blood")}
            defaultValue={values.blood}
          />
          <br />
          <TextField
            hintText="Enter your Allergies"
            floatingLabelText="Allergies"
            onChange={handleChange("allergies")}
            defaultValue={values.allergies}
          />
          <br />
          <TextField
            hintText="Enter your Medical Conditions"
            floatingLabelText="Medical Conditions"
            onChange={handleChange("conditions")}
            defaultValue={values.conditions}
          />
          <br />
          <TextField
            hintText="Enter your Medications"
            floatingLabelText="Medications"
            onChange={handleChange("medications")}
            defaultValue={values.medications}
          />
          <br />
          <RaisedButton
            label="Continue"
            primary={true}
            style={styles.button}
            onClick={this.continue}
          />
          <RaisedButton
            label="Back"
            primary={false}
            style={styles.button}
            onClick={this.back}
          />
        </React.Fragment>
      </MuiThemeProvider>
    );
  }
}

const styles = {
  button: {
    margin: 15
  }
};

export default FormPersonalDetails;
