import React, { Component } from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import AppBar from "material-ui/AppBar";

export class Success extends Component {
  render() {
    return (
      <MuiThemeProvider>
        <React.Fragment>
          <AppBar title="Successful Entry" />
          <h1>Thank You For Submitting This Information</h1>
          <p>We greatly appreciate the trust you have placed upon us.</p>
        </React.Fragment>
      </MuiThemeProvider>
    );
  }
}

export default Success;
